'use client';

import { useState } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { BellIcon, CheckCircleIcon, ClockIcon, CurrencyDollarIcon } from '@heroicons/react/24/outline';
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';

// Mock user data
const userData = {
  name: 'أحمد محمد',
  avatar: '/images/providers/provider1.jpg',
  bio: 'مصمم صور ذكاء اصطناعي مع خبرة أكثر من 3 سنوات في استخدام مختلف الأدوات والمنصات.',
  joinDate: 'أبريل 2023',
  rating: 4.9,
  completedJobs: 87,
  offeredServices: ['توليد صور', 'توليد فيديو'],
};

// Mock service requests data
const serviceRequests = [
  {
    id: 1,
    clientName: 'محمد علي',
    requestDate: '12 مارس 2025',
    description: 'أحتاج إلى تصميم 5 صور لموقع تجاري بأسلوب واقعي',
    status: 'pending',
  },
  {
    id: 2,
    clientName: 'سارة أحمد',
    requestDate: '10 مارس 2025',
    description: 'فيديو توضيحي لمنتج جديد بطول 30 ثانية',
    status: 'in-progress',
  },
  {
    id: 3,
    clientName: 'خالد محمود',
    requestDate: '5 مارس 2025',
    description: 'تصميم شخصيات كرتونية لقصة أطفال',
    status: 'completed',
    rating: 5,
  },
];

// Mock earnings data
const earningsData = {
  currentMonth: 1250,
  lastMonth: 980,
  pendingPayments: 450,
  totalEarned: 8720,
};

export default function ProfilePage() {
  const [activeTab, setActiveTab] = useState('requests');

  // Status badge component
  const StatusBadge = ({ status }: { status: string }) => {
    let bgColor, textColor, label;
    
    switch (status) {
      case 'pending':
        bgColor = 'bg-yellow-100';
        textColor = 'text-yellow-800';
        label = 'في انتظار الموافقة';
        break;
      case 'in-progress':
        bgColor = 'bg-blue-100';
        textColor = 'text-blue-800';
        label = 'قيد التنفيذ';
        break;
      case 'completed':
        bgColor = 'bg-green-100';
        textColor = 'text-green-800';
        label = 'مكتمل';
        break;
      default:
        bgColor = 'bg-gray-100';
        textColor = 'text-gray-800';
        label = 'غير معروف';
    }
    
    return (
      <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${bgColor} ${textColor}`}>
        {label}
      </span>
    );
  };

  return (
    <>
      <Navbar />
      <div className="bg-gray-50 py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="bg-white shadow rounded-lg overflow-hidden">
            {/* Profile header */}
            <div className="px-4 py-5 sm:px-6 border-b border-gray-200">
              <div className="flex items-center">
                <div className="h-16 w-16 rounded-full overflow-hidden bg-gray-200">
                  <Image
                    src={userData.avatar}
                    alt={userData.name}
                    width={64}
                    height={64}
                    className="h-full w-full object-cover"
                  />
                </div>
                <div className="mr-4">
                  <h1 className="text-2xl font-bold text-gray-900">{userData.name}</h1>
                  <p className="text-sm text-gray-500">عضو منذ {userData.joinDate}</p>
                </div>
                <div className="mr-auto flex items-center space-x-4 space-x-reverse">
                  <div className="flex items-center bg-gray-100 px-3 py-1 rounded-full">
                    <span className="text-yellow-500 ml-1">★</span>
                    <span className="text-gray-700">{userData.rating}</span>
                  </div>
                  <div className="text-gray-700 text-sm">
                    {userData.completedJobs} مشروع منجز
                  </div>
                </div>
              </div>
              <p className="mt-4 text-gray-700">{userData.bio}</p>
              <div className="mt-3 flex flex-wrap gap-2">
                {userData.offeredServices.map((service, index) => (
                  <span
                    key={index}
                    className="inline-flex items-center px-3 py-0.5 rounded-full text-sm font-medium bg-indigo-100 text-indigo-800"
                  >
                    {service}
                  </span>
                ))}
              </div>
            </div>
            
            {/* Tabs */}
            <div className="border-b border-gray-200">
              <nav className="flex -mb-px">
                <button
                  onClick={() => setActiveTab('requests')}
                  className={`mr-8 py-4 px-1 border-b-2 font-medium text-sm ${
                    activeTab === 'requests'
                      ? 'border-indigo-500 text-indigo-600'
                      : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                  }`}
                >
                  طلبات الخدمة
                </button>
                <button
                  onClick={() => setActiveTab('earnings')}
                  className={`mr-8 py-4 px-1 border-b-2 font-medium text-sm ${
                    activeTab === 'earnings'
                      ? 'border-indigo-500 text-indigo-600'
                      : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                  }`}
                >
                  الإيرادات
                </button>
                <button
                  onClick={() => setActiveTab('settings')}
                  className={`mr-8 py-4 px-1 border-b-2 font-medium text-sm ${
                    activeTab === 'settings'
                      ? 'border-indigo-500 text-indigo-600'
                      : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                  }`}
                >
                  إعدادات الحساب
                </button>
              </nav>
            </div>
            
            {/* Tab content */}
            <div className="p-6">
              {/* Service Requests Tab */}
              {activeTab === 'requests' && (
                <div>
                  <h2 className="text-lg font-medium text-gray-900 mb-4">طلبات الخدمة</h2>
                  
                  <div className="space-y-4">
                    {serviceRequests.map((request) => (
                      <div key={request.id} className="border border-gray-200 rounded-md p-4">
                        <div className="flex justify-between items-center">
                          <h3 className="text-md font-medium text-gray-900">
                            طلب من {request.clientName}
                          </h3>
                          <StatusBadge status={request.status} />
                        </div>
                        <p className="mt-2 text-sm text-gray-600">
                          {request.description}
                        </p>
                        <div className="mt-3 flex justify-between items-center">
                          <span className="text-xs text-gray-500">
                            تاريخ الطلب: {request.requestDate}
                          </span>
                          <div className="flex space-x-2 space-x-reverse">
                            {request.status === 'pending' && (
                              <>
                                <button className="inline-flex items-center px-3 py-1 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700">
                                  قبول
                                </button>
                                <button className="inline-flex items-center px-3 py-1 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50">
                                  رفض
                                </button>
                              </>
                            )}
                            {request.status === 'in-progress' && (
                              <button className="inline-flex items-center px-3 py-1 border border-transparent text-sm font-medium rounded-md text-white bg-green-600 hover:bg-green-700">
                                إكمال العمل
                              </button>
                            )}
                            {request.status === 'completed' && request.rating && (
                              <div className="flex items-center">
                                <span className="text-yellow-500 ml-1">{'★'.repeat(request.rating)}</span>
                                <span className="text-gray-700">{request.rating}/5</span>
                              </div>
                            )}
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              )}
              
              {/* Earnings Tab */}
              {activeTab === 'earnings' && (
                <div>
                  <h2 className="text-lg font-medium text-gray-900 mb-4">الإيرادات</h2>
                  
                  <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4">
                    <div className="bg-white overflow-hidden shadow rounded-lg">
                      <div className="px-4 py-5 sm:p-6">
                        <div className="flex items-center">
                          <div className="flex-shrink-0 bg-indigo-100 rounded-md p-3">
                            <CurrencyDollarIcon className="h-6 w-6 text-indigo-600" aria-hidden="true" />
                          </div>
                          <div className="mr-5">
                            <p className="text-sm font-medium text-gray-500">إيرادات الشهر الحالي</p>
                            <p className="text-2xl font-semibold text-gray-900">${earningsData.currentMonth}</p>
                          </div>
                        </div>
                      </div>
                    </div>
                    
                    <div className="bg-white overflow-hidden shadow rounded-lg">
                      <div className="px-4 py-5 sm:p-6">
                        <div className="flex items-center">
                          <div className="flex-shrink-0 bg-green-100 rounded-md p-3">
                            <CheckCircleIcon className="h-6 w-6 text-green-600" aria-hidden="true" />
                          </div>
                          <div className="mr-5">
                            <p className="text-sm font-medium text-gray-500">إيرادات الشهر الماضي</p>
                            <p className="text-2xl font-semibold text-gray-900">${earningsData.lastMonth}</p>
                          </div>
                        </div>
                      </div>
                    </div>
                    
                    <div className="bg-white overflow-hidden shadow rounded-lg">
                      <div className="px-4 py-5 sm:p-6">
                        <div className="flex items-center">
                          <div className="flex-shrink-0 bg-yellow-100 rounded-md p-3">
                            <ClockIcon className="h-6 w-6 text-yellow-600" aria-hidden="true" />
                          </div>
                          <div className="mr-5">
                            <p className="text-sm font-medium text-gray-500">مدفوعات قيد الانتظار</p>
                            <p className="text-2xl font-semibold text-gray-900">${earningsData.pendingPayments}</p>
                          </div>
                        </div>
                      </div>
                    </div>
                    
                    <div className="bg-white overflow-hidden shadow rounded-lg">
                      <div className="px-4 py-5 sm:p-6">
                        <div className="flex items-center">
                          <div className="flex-shrink-0 bg-purple-100 rounded-md p-3">
                            <CurrencyDollarIcon className="h-6 w-6 text-purple-600" aria-hidden="true" />
                          </div>
                          <div className="mr-5">
                            <p className="text-sm font-medium text-gray-500">إجمالي الإيرادات</p>
                            <p className="text-2xl font-semibold text-gray-900">${earningsData.totalEarned}</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  
                  <div className="mt-6">
                    <Link
                      href="#"
                      className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700"
                    >
                      طلب سحب الأموال
                    </Link>
                  </div>
                </div>
              )}
              
              {/* Settings Tab */}
              {activeTab === 'settings' && (
                <div>
                  <h2 className="text-lg font-medium text-gray-900 mb-4">إعدادات الحساب</h2>
                  <p className="text-gray-500">
                    لم يتم تنفيذ هذا القسم بعد. سيتضمن إعدادات تحديث معلومات الحساب وبيانات الدفع.
                  </p>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
} 